:root{
  --main: #005EFE;
}

/* page common */
/* ---------------- fonts ---------------- */
@font-face {
  font-family: Gilroy;
  src: url(../font/Gilroy_ExtraBold.otf);
}
@font-face {
  font-family: Gotham_Narrow;
  src: url(../font/Gotham_Narrow_Book.otf);
}
@font-face {
  font-family: Gotham_Narrow;
  src: url(../font/Gotham_Narrow_Bold.otf);
  font-weight: bold;
}
@font-face {
  font-family: Gotham_Narrow;
  src: url(../font/Gotham_Narrow_Light.otf);
  font-weight: light;
}
.font-gilroy{
  font-family: Gilroy;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: .1em;
}

html{
  font-family: Gotham_Narrow;
  line-height: 1.5;
  color: #222;
  font-size: 16px;
  font-size: calc(8px + .8vw);
  overflow-x: hidden;
}
@media screen and (max-width: 1000px){
  html {
    font-size: 16px;
  }
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /*user-select: none;*/
}
a{
  text-decoration: none;
  color: inherit;
  opacity: .8;
  transition: .2s opacity ease;
}
a:hover{
  opacity: 1;
}
p, li{
  color: #999;
}
:focus{
  outline: none;
}
.flex-row{
  display: flex;
  align-items: center;
}
.flex-item{
  margin: 0 20px;
}
.flex-row-even > * {
  flex: 1;
}
.flex-space{
  flex: 1;
}
.round{
  border-radius: 50%;
}
.full-width{
  width: 100%;
}
.center{
  text-align: center;
}
.bold-small{
  font-weight: bold;
  color: #222;
}

/* ---------------- header ---------------- */
header{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  padding: 20px 100px;
  background: #fff;
  z-index: 10;
}

.nav-item{
  display: inline-block;
  font-size: .8em;
  margin: 8px 20px;
  letter-spacing: .2em;
}


/* ---------------- footer ---------------- */
footer{
  margin-bottom: 30px;
  padding: 40px 100px 0;
  align-items: center;
}
.footer-copy p{
  margin: 8px 0;
}

/* ---------------- responsive ---------------- */
@media screen and (max-width: 800px){
  header, footer{
    padding: 20px 40px;
  }
}
@media screen and (max-width: 700px) {
  .footer-copy{
    flex-direction: column;
  }
  .footer-text{
    order: 2;
  }
}
@media screen and (max-width: 480px){
  header{
    padding: 20px 12px;
  }
  .logo{
    width: 36px;
  }
  .nav-item{
    margin: 8px;
  }
}

section{
  padding: 80px 100px;
  padding: 80px calc(50vw - 500px);
}
@media screen and (max-width: 1000px){
  section{
    padding: 50px 40px;
  }
}

.bg-shading{
  background: #F5FAFF;
}
.number{
  color: var(--main);
  font-size: 1.8em;
  font-weight: normal;
}
.section-type{
  font-size: .8em;
  text-align: center;
  color: var(--main);
  letter-spacing: .3em;
}
h1{
  font-size: 2em;
  text-transform: capitalize;
  text-align: center;
  font-weight: normal;
  margin: .3em;
}
h2{
  font-weight: normal;
  margin: 1em 0 .3em;
  text-transform: capitalize;
}
h2:before{
  content: '';
  display: block;
  height: 4px;
  width: 100px;
  background: var(--main);
  margin-bottom: .5em;
}
h1 + p {
  text-align: center;
}
ul{
  margin: 20px;
}

/* intro */
.intro{
  width: 80%;
  margin: 100px auto 0;
}

.project-title{
  font-size: 2.5em;
}
@media screen and (max-width: 800px){
  .intro{
    width: 100%;
    margin: 80px auto 0;
  }
  .project-title{
    font-size: 2em;
  }
}
.project-cover{
  width: 100%;
  transform: scale(1.2);
}

.section-info{
  padding-top: 200px;
  font-size: .9em;
}
.section-info .flex-row{
  align-items: flex-start;
}
.info-title{
  color: #222;
}


.quote-wrap{
  margin: 2em 0;
}
.quote-wrap > p{
  padding-left: 1.5em;
}
p.quote-title{
  padding-left: .8em;
  font-size: 1.2em;
  border-left: 4px solid var(--main);
  font-weight: light;
  margin: .3em 0;
}
